// Imports
import {
    TimelineMax,
    Back,
    Power3
} from "gsap";
import Splitting from 'splitting'

export default class Loader {
    constructor(el) {
        this.container = el;
        this.result = null;
    }

    init() {
        this.domReady().then(() => {
                this.result = Splitting()
            })
            .then(() => {
                this.animation()
            })
    }

    domReady() {
        return new Promise(resolve => {
            document.addEventListener('DOMContentLoaded', resolve)
        })
    }

    animation() {
        // console.log(this.result)
        const tl = new TimelineMax({
            delay: .4
        });

        tl.staggerTo(this.result[0].chars, 1, {
                y: 0,
                ease: Back.easeOut.config(1.2),
            }, .2)
            .to('.overlay1', 1, {
                scaleY: 1,
                transformOrigin: '50% 100%',
                ease: Power3.easeInOut,
            })
            .to('.overlay2', 1, {
                scaleY: 1,
                transformOrigin: '50% 100%',
                ease: Power3.easeInOut,
            }, '+=.2')
            .to('.loader__img', .7, {
                scale: 1,
                ease: Back.easeOut.config(1.7),
            })
            .to('.loader', .5, {
                    autoAlpha: 0
                },
                '+= .5')
            .set('.cursor--small', {
                background: '#fff'
            })
    }
}